.verses {
    display: grid;
    text-align: left;
    padding: 20px 20px 20px 20px;
    margin-top: 0;
    background-color: #066163;
    color: white;
    cursor: pointer;
    overflow: auto;
}
.surah {
    margin-left: 600px;
    font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
    font-size: xxx-large;
    color: white;
}

.bis {
    text-align: center;
}

.text {
    font-size: x-large;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
        sans-serif;
color: white;
    text-decoration: double;


}
.bism {
    margin-left: 600px;
    margin-bottom: 20px;
    margin-top: -9px;
    font-size: xx-large;
}
.right {
    text-align: right;
    font-size: xx-large;
    font-family: "Bebas Neue", cursive;
}
.right:hover {
  
}

.left {
    font-size: x-large;
    margin-top: 50px;
}
.left:hover {
 
}

@media only screen and (max-width: 1000px) {
    .verses {
        justify-content: left;
        text-align: left;
        color: white;
        padding: 20px 20px 20px 20px;
        margin-top: 30px;
        background-color: #066163;
    }
    .surah {
        margin-left: 70px;
        text-align: center;
        font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
        font-size: xxx-large;
        color: white;
    }
    .text {
        font-size: large;
        color: white;
        font-family: "Times New Roman", Times, serif;
    }
    .bis {
        text-align: center;
    }
    .bism {
        display: none;
        margin-bottom: 20px;
        margin-top: -9px;
    }
}

.controls button {
    background-color: transparent;
}


